@import "template.style.scss";
@import "nav.scss";
@import "intro.scss";

body{
  margin: 0;
  background-color: #fff;

  font-family: 'Oswald', sans-serif;
  font-size: 14px;
  line-height: 1.3;
  color: #444;
}

*,
*:before,
*:after{
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6{
  margin: 0;
}

p{
  margin: 0 0 10px;
}

ul, ol{
  margin: 0;
  padding: 0;
  list-style: none;
}

.img{
  display: block;
  max-width: 100%;
  height: auto;
}

.container{
  width: 100%;
  max-width: 310px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 768px){
  .container{
    max-width: 738px;
    padding: 0 15px;
  }
}

@media (min-width: 1200px){
  .container{
    min-width: 1200px;
    padding: 0 15px;
  }
}
